/* just for jsfiddle */


@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: local('Material Icons'), local('MaterialIcons-Regular'), url(https://fonts.gstatic.com/s/materialicons/v18/2fcrYFNaTjcS6g4U3t-Y5ZjZjT5FdEJ140U2DJYC3mY.woff2) format('woff2');
  }
  
  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .middle-indicator{
     position:absolute;
     top:50%;
     }
    .middle-indicator-text{
     font-size: 4.2rem;
    }
   
  .content-indicator{
      width: 64px;
      height: 64px;
      background: none;
      -moz-border-radius: 50px;
      -webkit-border-radius: 50px;
      border-radius: 50px; 
    }
      .indicators{
        visibility: hidden;
    }
  
img{
    height:350px;
    width:100%;
}


.story::-webkit-scrollbar {
    width: 12px;
}

.story::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px indigo; 
    border-radius: 10px;
}

.story::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px indigo; 
}


@media only screen and (min-width : 1000px){
    .story{
        height:400px;
        overflow:auto;
        overflow-x:hidden;
    }
    
} 